import { useState, useEffect, useRef } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useTranslation } from "react-i18next";
import mapboxgl from "mapbox-gl";
import "mapbox-gl/dist/mapbox-gl.css";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Label } from "@/components/ui/label";
import { Checkbox } from "@/components/ui/checkbox";
import { Switch } from "@/components/ui/switch";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Upload, X, MapPin, Globe, Store, Clock, Phone, Globe as WebIcon, Key, ImageIcon, ShoppingBag, UtensilsCrossed, User } from "lucide-react";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Card, CardContent } from "@/components/ui/card";
import { supabase } from "@/integrations/supabase/client";
import { useToast } from "@/hooks/use-toast";
import { useQueryClient } from "@tanstack/react-query";
import { useUserRole } from "@/hooks/useUserRole";
import { DishImageManager } from "@/components/admin/DishImageManager";
import { RichTextEditor } from "@/components/admin/RichTextEditor";
import { TypeCombobox } from "@/components/admin/TypeCombobox";
import { useAllRestaurantTypes } from "@/hooks/useAllRestaurantTypes";
import i18n from "@/i18n/config";

const restaurantSchema = z.object({
  name: z.string().min(1, i18n.t("validation.nameRequired")),
  name_en: z.string().min(1, i18n.t("validation.nameRequired")),
  name_pt: z.string().min(1, i18n.t("validation.nameRequired")),
  key_id: z.string().optional(),
  type: z.array(z.string()).min(1, i18n.t("validation.typeRequired")).max(3, i18n.t("validation.typeMaximum")),
  type_en: z.array(z.string()).min(1, i18n.t("validation.typeRequired")).max(3, i18n.t("validation.typeMaximum")),
  type_pt: z.array(z.string()).min(1, i18n.t("validation.typeRequired")).max(3, i18n.t("validation.typeMaximum")),
  description: z.string().optional(),
  description_en: z.string().optional(),
  description_pt: z.string().optional(),
  announcement_zh: z.string().optional(),
  announcement_en: z.string().optional(),
  announcement_pt: z.string().optional(),
  address: z.string().min(1, i18n.t("validation.addressRequired")),
  lat: z.number(),
  lng: z.number(),
  phone: z.string().optional(),
  website_url: z.string().optional(),
  visible: z.boolean().default(true),
  show_takeaway_button: z.boolean().default(true),
  show_booking_button: z.boolean().default(true),
});

type RestaurantFormData = z.infer<typeof restaurantSchema>;

interface RestaurantFormDialogProps {
  restaurant?: any;
  trigger: React.ReactNode;
}

interface DayHours {
  open: string;
  close: string;
  closed: boolean;
  splitShift?: boolean; // 分段营业模式
  open2?: string; // 第二时段开门时间
  close2?: string; // 第二时段关门时间
}

const DAYS = ["monday", "tuesday", "wednesday", "thursday", "friday", "saturday", "sunday"];

export function RestaurantFormDialog({ restaurant, trigger }: RestaurantFormDialogProps) {
  const { t } = useTranslation();
  const { toast } = useToast();
  const queryClient = useQueryClient();
  const { isAdmin } = useUserRole();
  const { data: existingTypes } = useAllRestaurantTypes();
  const [open, setOpen] = useState(false);
  const [imageFile, setImageFile] = useState<File | null>(null);
  const [imagePreview, setImagePreview] = useState<string | null>(restaurant?.image_url || null);
  const [coverImageFile, setCoverImageFile] = useState<File | null>(null);
  const [coverImagePreview, setCoverImagePreview] = useState<string | null>(restaurant?.cover_image_url || null);
  const [ownerAvatarFile, setOwnerAvatarFile] = useState<File | null>(null);
  const [ownerAvatarPreview, setOwnerAvatarPreview] = useState<string | null>(restaurant?.owner_avatar_url || null);
  const [ownerBio, setOwnerBio] = useState<string>(restaurant?.owner_bio || "");
  const [ownerBioEn, setOwnerBioEn] = useState<string>(restaurant?.owner_bio_en || "");
  const [ownerBioPt, setOwnerBioPt] = useState<string>(restaurant?.owner_bio_pt || "");
  const [isSubmitting, setIsSubmitting] = useState(false);
  const mapContainer = useRef<HTMLDivElement>(null);
  const map = useRef<mapboxgl.Map | null>(null);
  const marker = useRef<mapboxgl.Marker | null>(null);
  const [openingHours, setOpeningHours] = useState<Record<string, DayHours>>(() => {
    const defaultHours: Record<string, DayHours> = {};
    DAYS.forEach(day => {
      defaultHours[day] = { 
        open: "12:00", 
        close: "23:00", 
        closed: false,
        splitShift: false,
        open2: "19:00",
        close2: "23:00"
      };
    });
    if (restaurant?.opening_hours) {
      return { ...defaultHours, ...restaurant.opening_hours };
    }
    return defaultHours;
  });
  
  // State for template type
  const [template, setTemplate] = useState<string>(restaurant?.template || 'default');
  
  // State for multiple types (1-3 tags)
  const [types, setTypes] = useState<{ zh: string; en: string; pt: string }[]>(() => {
    if (restaurant?.type && Array.isArray(restaurant.type)) {
      return restaurant.type.map((zh: string, idx: number) => ({
        zh,
        en: restaurant.type_en?.[idx] || "",
        pt: restaurant.type_pt?.[idx] || "",
      }));
    }
    return [{ zh: "", en: "", pt: "" }];
  });

  const {
    register,
    handleSubmit,
    formState: { errors },
    setValue,
    watch,
    trigger: triggerValidation,
  } = useForm<RestaurantFormData>({
    resolver: zodResolver(restaurantSchema),
    defaultValues: restaurant ? {
      name: restaurant.name,
      name_en: restaurant.name_en || "",
      name_pt: restaurant.name_pt || "",
      key_id: restaurant.key_id || "",
      type: Array.isArray(restaurant.type) ? restaurant.type : [restaurant.type],
      type_en: Array.isArray(restaurant.type_en) ? restaurant.type_en : (restaurant.type_en ? [restaurant.type_en] : [""]),
      type_pt: Array.isArray(restaurant.type_pt) ? restaurant.type_pt : (restaurant.type_pt ? [restaurant.type_pt] : [""]),
      description: restaurant.description || "",
      description_en: restaurant.description_en || "",
      description_pt: restaurant.description_pt || "",
      announcement_zh: restaurant.announcement_zh || "",
      announcement_en: restaurant.announcement_en || "",
      announcement_pt: restaurant.announcement_pt || "",
      address: restaurant.address,
      lat: typeof restaurant.lat === 'number' && !isNaN(restaurant.lat) ? restaurant.lat : 38.7223,
      lng: typeof restaurant.lng === 'number' && !isNaN(restaurant.lng) ? restaurant.lng : -9.1393,
      phone: restaurant.phone || "",
      website_url: restaurant.website_url || "",
      visible: restaurant.visible ?? true,
      show_takeaway_button: restaurant.show_takeaway_button ?? true,
      show_booking_button: restaurant.show_booking_button ?? true,
    } : {
      name: "",
      name_en: "",
      name_pt: "",
      key_id: "",
      type: [""],
      type_en: [""],
      type_pt: [""],
      description: "",
      description_en: "",
      description_pt: "",
      announcement_zh: "",
      announcement_en: "",
      announcement_pt: "",
      address: "",
      lat: 38.7223,
      lng: -9.1393,
      phone: "",
      website_url: "",
      visible: true,
      show_takeaway_button: true,
      show_booking_button: true,
    },
  });

  // Helper to show validation errors
  const showValidationErrors = () => {
    const errorMessages: string[] = [];
    
    if (errors.name) errorMessages.push(t("admin.fieldName") + " (中文)");
    if (errors.name_en) errorMessages.push(t("admin.fieldName") + " (EN)");
    if (errors.name_pt) errorMessages.push(t("admin.fieldName") + " (PT)");
    if (errors.type) errorMessages.push(t("admin.fieldType") + " (中文)");
    if (errors.type_en) errorMessages.push(t("admin.fieldType") + " (EN)");
    if (errors.type_pt) errorMessages.push(t("admin.fieldType") + " (PT)");
    if (errors.address) errorMessages.push(t("admin.fieldAddress"));
    if (errors.lat || errors.lng) errorMessages.push(t("admin.fieldLocation"));
    
    if (errorMessages.length > 0) {
      toast({
        title: t("admin.validationFailed"),
        description: t("admin.missingFields") + ": " + errorMessages.join(", "),
        variant: "destructive",
      });
    }
  };
  
  const addType = () => {
    if (types.length < 3) {
      setTypes([...types, { zh: "", en: "", pt: "" }]);
    }
  };
  
  const removeType = (index: number) => {
    if (types.length > 1) {
      setTypes(types.filter((_, i) => i !== index));
    }
  };
  
  const updateType = (index: number, field: 'zh' | 'en' | 'pt', value: string) => {
    const newTypes = [...types];
    newTypes[index][field] = value;
    setTypes(newTypes);
    
    // Update form values
    setValue('type', newTypes.map(t => t.zh).filter(Boolean));
    setValue('type_en', newTypes.map(t => t.en).filter(Boolean));
    setValue('type_pt', newTypes.map(t => t.pt).filter(Boolean));
  };

  const lat = watch("lat");
  const lng = watch("lng");
  const visible = watch("visible");
  const showTakeawayButton = watch("show_takeaway_button");
  const showBookingButton = watch("show_booking_button");

  // Helper function to check if coordinates are valid
  const isValidCoordinate = (lat: number, lng: number) => {
    return typeof lat === 'number' && typeof lng === 'number' && 
           !isNaN(lat) && !isNaN(lng) && 
           isFinite(lat) && isFinite(lng);
  };

  // Initialize map when dialog opens
  useEffect(() => {
    if (!open) {
      // Clean up map when dialog closes
      if (map.current) {
        map.current.remove();
        map.current = null;
        marker.current = null;
      }
      return;
    }

    // Don't initialize if already exists
    if (map.current) return;

    const mapboxToken = import.meta.env.VITE_MAPBOX_TOKEN;
    if (!mapboxToken) {
      console.error("Mapbox token not found");
      toast({
        title: t("common.error"),
        description: "Mapbox token not configured",
        variant: "destructive",
      });
      return;
    }

    // Wait for container to be rendered
    const initMap = () => {
      if (!mapContainer.current) {
        console.error("Map container not found");
        return;
      }

      try {
        mapboxgl.accessToken = mapboxToken;

        // Use valid coordinates or default to Lisbon
        const centerLng = isValidCoordinate(lat, lng) ? lng : -9.1393;
        const centerLat = isValidCoordinate(lat, lng) ? lat : 38.7223;

        map.current = new mapboxgl.Map({
          container: mapContainer.current,
          style: "mapbox://styles/mapbox/light-v11",
          center: [centerLng, centerLat],
          zoom: 15,
        });

        // Disable scroll zoom by default (require Ctrl key)
        map.current.scrollZoom.disable();

        map.current.on('load', () => {
          if (!map.current) return;
          
          // Add marker after map loads only if coordinates are valid
          if (isValidCoordinate(lat, lng)) {
            marker.current = new mapboxgl.Marker({ draggable: true })
              .setLngLat([lng, lat])
              .addTo(map.current);
          }

          marker.current.on("dragend", () => {
            const lngLat = marker.current!.getLngLat();
            setValue("lng", lngLat.lng);
            setValue("lat", lngLat.lat);
          });

          // Resize to fit container
          setTimeout(() => {
            map.current?.resize();
          }, 100);
        });

        // Enable scroll zoom only when Ctrl key is pressed
        const handleWheel = (e: WheelEvent) => {
          if (e.ctrlKey || e.metaKey) {
            map.current?.scrollZoom.enable();
          } else {
            map.current?.scrollZoom.disable();
          }
        };

        mapContainer.current.addEventListener('wheel', handleWheel);

        map.current.on("click", (e) => {
          const { lng, lat } = e.lngLat;
          setValue("lng", lng);
          setValue("lat", lat);
          marker.current?.setLngLat([lng, lat]);
        });

        map.current.on('error', (e) => {
          console.error("Map error:", e);
        });
      } catch (error) {
        console.error("Failed to initialize map:", error);
      }
    };

    // Give time for dialog animation and container to render
    const timer = setTimeout(initMap, 500);

    return () => {
      clearTimeout(timer);
    };
  }, [open, lat, lng]);

  // Update marker position when lat/lng changes
  useEffect(() => {
    if (marker.current && map.current && isValidCoordinate(lat, lng)) {
      marker.current.setLngLat([lng, lat]);
      map.current.setCenter([lng, lat]);
    }
  }, [lat, lng]);

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleCoverImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setCoverImageFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setCoverImagePreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleOwnerAvatarChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setOwnerAvatarFile(file);
      const reader = new FileReader();
      reader.onloadend = () => {
        setOwnerAvatarPreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const uploadImage = async (): Promise<string | null> => {
    if (!imageFile) return restaurant?.image_url || null;

    const fileExt = imageFile.name.split(".").pop();
    const fileName = `${Math.random()}.${fileExt}`;
    const filePath = `${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from("restaurant-images")
      .upload(filePath, imageFile);

    if (uploadError) {
      throw uploadError;
    }

    const { data: { publicUrl } } = supabase.storage
      .from("restaurant-images")
      .getPublicUrl(filePath);

    return publicUrl;
  };

  const uploadCoverImage = async (): Promise<string | null> => {
    if (!coverImageFile) return restaurant?.cover_image_url || null;

    const fileExt = coverImageFile.name.split(".").pop();
    const fileName = `cover_${Math.random()}.${fileExt}`;
    const filePath = `${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from("restaurant-images")
      .upload(filePath, coverImageFile);

    if (uploadError) {
      throw uploadError;
    }

    const { data: { publicUrl } } = supabase.storage
      .from("restaurant-images")
      .getPublicUrl(filePath);

    return publicUrl;
  };

  const uploadOwnerAvatar = async (): Promise<string | null> => {
    if (!ownerAvatarFile) return restaurant?.owner_avatar_url || null;

    const fileExt = ownerAvatarFile.name.split(".").pop();
    const fileName = `owner_${Math.random()}.${fileExt}`;
    const filePath = `${fileName}`;

    const { error: uploadError } = await supabase.storage
      .from("restaurant-images")
      .upload(filePath, ownerAvatarFile);

    if (uploadError) {
      throw uploadError;
    }

    const { data: { publicUrl } } = supabase.storage
      .from("restaurant-images")
      .getPublicUrl(filePath);

    return publicUrl;
  };

  const onSubmit = async (data: RestaurantFormData) => {
    setIsSubmitting(true);
    try {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) throw new Error("Not authenticated");

      const imageUrl = await uploadImage();
      const coverImageUrl = await uploadCoverImage();
      const ownerAvatarUrl = await uploadOwnerAvatar();

      const restaurantData = {
        name: data.name,
        name_en: data.name_en,
        name_pt: data.name_pt,
        key_id: data.key_id || null,
        type: data.type,
        type_en: data.type_en,
        type_pt: data.type_pt,
        description: data.description || null,
        description_en: data.description_en || null,
        description_pt: data.description_pt || null,
        announcement_zh: data.announcement_zh || null,
        announcement_en: data.announcement_en || null,
        announcement_pt: data.announcement_pt || null,
        address: data.address,
        lat: template === 'ghost' ? (data.lat || 38.7223) : data.lat,
        lng: template === 'ghost' ? (data.lng || -9.1393) : data.lng,
        phone: data.phone || null,
        website_url: data.website_url || null,
        visible: data.visible,
        show_takeaway_button: data.show_takeaway_button,
        show_booking_button: template === 'ghost' ? false : data.show_booking_button,
        image_url: imageUrl,
        cover_image_url: coverImageUrl,
        opening_hours: template === 'ghost' ? null : openingHours as any,
        template: template,
        owner_avatar_url: ownerAvatarUrl,
        owner_bio: ownerBio || null,
        owner_bio_en: ownerBioEn || null,
        owner_bio_pt: ownerBioPt || null,
      };

      if (restaurant) {
        const { error } = await supabase
          .from("restaurants")
          .update({ ...restaurantData, updated_by: user.id })
          .eq("id", restaurant.id);

        if (error) throw error;

        toast({
          title: t("common.success"),
          description: t("admin.restaurantUpdated"),
        });
      } else {
        const { error } = await supabase
          .from("restaurants")
          .insert([{ ...restaurantData, created_by: user.id }]);

        if (error) throw error;

        toast({
          title: t("common.success"),
          description: t("admin.restaurantCreated"),
        });
      }

      queryClient.invalidateQueries({ queryKey: ["restaurants"] });
      setOpen(false);
    } catch (error: any) {
      toast({
        title: t("common.error"),
        description: error.message,
        variant: "destructive",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>{trigger}</DialogTrigger>
      <DialogContent className="max-w-[95vw] sm:max-w-6xl max-h-[95vh] overflow-hidden flex flex-col p-0">
        <DialogHeader className="px-6 pt-6 pb-4 border-b bg-muted/30">
          <DialogTitle className="text-2xl flex items-center gap-2">
            <Store className="w-6 h-6 text-primary" />
            {restaurant ? t("admin.editRestaurant") : t("admin.addRestaurant")}
          </DialogTitle>
          <DialogDescription className="text-base">
            {restaurant ? t("admin.editRestaurantDetails") : t("admin.createRestaurantWithInfo")}
          </DialogDescription>
        </DialogHeader>

        <ScrollArea className="px-6 h-[calc(95vh-220px)]">
          <form onSubmit={handleSubmit(onSubmit, showValidationErrors)} className="space-y-6 py-6">
            {/* Visibility Toggle - At the very top */}
            <Card className="border-primary/20 bg-gradient-to-br from-primary/5 to-background">
              <CardContent className="p-4">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className="p-2 rounded-lg bg-primary/10">
                      <Globe className="w-5 h-5 text-primary" />
                    </div>
                    <div>
                      <Label className="text-base font-semibold">{t("admin.frontendDisplay")}</Label>
                      <p className="text-xs text-muted-foreground">{t("admin.frontendDisplayDesc")}</p>
                    </div>
                  </div>
                  <Switch
                    checked={visible}
                    onCheckedChange={(checked) => setValue("visible", checked)}
                  />
                </div>
              </CardContent>
            </Card>

            {/* Template Selection */}
            <Card className="border-primary/20 bg-gradient-to-br from-background to-muted/20">
              <CardContent className="p-6">
                <Label className="text-base font-semibold flex items-center gap-2 mb-4">
                  <Store className="w-5 h-5 text-primary" />
                  {t("admin.restaurantTemplate")}
                </Label>
                <RadioGroup
                  value={template}
                  onValueChange={setTemplate}
                  className="grid grid-cols-1 md:grid-cols-2 gap-4"
                >
                  <div className="flex items-start space-x-3 p-4 rounded-lg border border-border hover:border-primary/50 transition-colors cursor-pointer" onClick={() => setTemplate('default')}>
                    <RadioGroupItem value="default" id="template-default" className="mt-1" />
                    <div className="flex-1">
                      <Label htmlFor="template-default" className="text-sm font-medium cursor-pointer">
                        {t("admin.templateDefault")}
                      </Label>
                      <p className="text-xs text-muted-foreground mt-1">
                        {t("admin.templateDefaultDesc")}
                      </p>
                    </div>
                  </div>
                  <div className="flex items-start space-x-3 p-4 rounded-lg border border-border hover:border-primary/50 transition-colors cursor-pointer" onClick={() => setTemplate('ghost')}>
                    <RadioGroupItem value="ghost" id="template-ghost" className="mt-1" />
                    <div className="flex-1">
                      <Label htmlFor="template-ghost" className="text-sm font-medium cursor-pointer">
                        {t("admin.templateGhost")}
                      </Label>
                      <p className="text-xs text-muted-foreground mt-1">
                        {t("admin.templateGhostDesc")}
                      </p>
                    </div>
                  </div>
                </RadioGroup>
              </CardContent>
            </Card>

            {/* Image Upload */}
            <Card className="border-primary/20 bg-gradient-to-br from-background to-muted/20">
              <CardContent className="p-6">
                <Label className="text-base font-semibold flex items-center gap-2 mb-4">
                  <Upload className="w-5 h-5 text-primary" />
                  {t("admin.restaurantImages")}
                </Label>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {/* Card Image - for restaurant list */}
                  <div className="space-y-3">
                    <div className="flex items-center gap-2">
                      <span className="text-sm font-medium">{t("admin.cardImage")}</span>
                      <span className="text-xs text-muted-foreground">({t("admin.cardImageHint")})</span>
                    </div>
                    <div className="flex items-center gap-4">
                      {imagePreview && (
                        <div className="relative group">
                          <img
                            src={imagePreview}
                            alt="Card Preview"
                            className="w-32 h-32 object-cover rounded-lg shadow-lg ring-2 ring-primary/20 transition-transform duration-200 group-hover:scale-105"
                          />
                          <Button
                            type="button"
                            variant="destructive"
                            size="icon"
                            className="absolute -top-2 -right-2 h-6 w-6 shadow-lg opacity-0 group-hover:opacity-100 transition-opacity"
                            onClick={() => {
                              setImageFile(null);
                              setImagePreview(null);
                            }}
                          >
                            <X className="h-3 w-3" />
                          </Button>
                        </div>
                      )}
                      <Label htmlFor="image-upload" className="cursor-pointer">
                        <div className="flex flex-col items-center gap-2 px-6 py-4 border-2 border-dashed border-primary/30 rounded-lg hover:border-primary hover:bg-primary/5 transition-all duration-200">
                          <Upload className="h-6 w-6 text-primary" />
                          <div className="text-center">
                            <p className="font-medium text-xs">{t("admin.uploadImage")}</p>
                          </div>
                        </div>
                        <Input
                          id="image-upload"
                          type="file"
                          accept="image/*"
                          className="hidden"
                          onChange={handleImageChange}
                        />
                      </Label>
                    </div>
                  </div>

                  {/* Cover Image - for detail page background */}
                  <div className="space-y-3">
                    <div className="flex items-center gap-2">
                      <span className="text-sm font-medium">{t("admin.coverImage")}</span>
                      <span className="text-xs text-muted-foreground">({t("admin.coverImageHint")})</span>
                    </div>
                    <div className="flex items-center gap-4">
                      {coverImagePreview && (
                        <div className="relative group">
                          <img
                            src={coverImagePreview}
                            alt="Cover Preview"
                            className="w-32 h-32 object-cover rounded-lg shadow-lg ring-2 ring-accent/20 transition-transform duration-200 group-hover:scale-105"
                          />
                          <Button
                            type="button"
                            variant="destructive"
                            size="icon"
                            className="absolute -top-2 -right-2 h-6 w-6 shadow-lg opacity-0 group-hover:opacity-100 transition-opacity"
                            onClick={() => {
                              setCoverImageFile(null);
                              setCoverImagePreview(null);
                            }}
                          >
                            <X className="h-3 w-3" />
                          </Button>
                        </div>
                      )}
                      <Label htmlFor="cover-image-upload" className="cursor-pointer">
                        <div className="flex flex-col items-center gap-2 px-6 py-4 border-2 border-dashed border-accent/30 rounded-lg hover:border-accent hover:bg-accent/5 transition-all duration-200">
                          <ImageIcon className="h-6 w-6 text-accent" />
                          <div className="text-center">
                            <p className="font-medium text-xs">{t("admin.uploadImage")}</p>
                          </div>
                        </div>
                        <Input
                          id="cover-image-upload"
                          type="file"
                          accept="image/*"
                          className="hidden"
                          onChange={handleCoverImageChange}
                        />
                      </Label>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Dish Images - Only show when editing existing restaurant */}
            {restaurant?.id && (
              <Card className="border-primary/20">
                <CardContent className="p-6">
                  <div className="flex items-center justify-between mb-4">
                    <Label className="text-base font-semibold flex items-center gap-2">
                      <ImageIcon className="w-5 h-5 text-primary" />
                      {t("admin.dishGallery")}
                    </Label>
                  </div>
                  <DishImageManager restaurantId={restaurant.id} />
                </CardContent>
              </Card>
            )}

            {/* Multi-language Content with Tabs */}
            <Card className="border-primary/20">
              <CardContent className="p-6">
                <Label className="text-base font-semibold flex items-center gap-2 mb-4">
                  <Globe className="w-5 h-5 text-primary" />
                  {t("admin.multilingualContent")}
                </Label>
                <Tabs defaultValue="zh" className="w-full">
                  <TabsList className="grid w-full grid-cols-3 mb-6">
                    <TabsTrigger value="zh" className="gap-2">
                      🇨🇳 {t("admin.chinese")}
                    </TabsTrigger>
                    <TabsTrigger value="en" className="gap-2">
                      🇬🇧 {t("admin.english")}
                    </TabsTrigger>
                    <TabsTrigger value="pt" className="gap-2">
                      🇵🇹 {t("admin.portuguese")}
                    </TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="zh" className="space-y-4 mt-0">
                    <div className="space-y-2">
                      <Label htmlFor="name" className="text-sm font-medium">{t("admin.restaurantName")}</Label>
                      <Input 
                        id="name" 
                        {...register("name")} 
                        placeholder={t("admin.enterRestaurantName")}
                        className="text-base"
                      />
                      {errors.name && (
                        <p className="text-sm text-destructive">{errors.name.message}</p>
                      )}
                    </div>
                    <div className="space-y-3">
                      <div className="flex justify-between items-center">
                        <Label className="text-sm font-medium">{t("admin.restaurantType")}</Label>
                        {types.length < 3 && (
                          <Button type="button" variant="outline" size="sm" onClick={addType}>
                            {t("admin.addType")}
                          </Button>
                        )}
                      </div>
                      {types.map((type, index) => (
                        <div key={index} className="flex gap-2 items-center">
                          <TypeCombobox
                            placeholder={t("admin.typeExample")}
                            value={type.zh}
                            onChange={(value) => updateType(index, 'zh', value)}
                            suggestions={existingTypes?.uniqueZh || []}
                          />
                          <Button
                            type="button"
                            variant="ghost"
                            size="icon"
                            onClick={() => removeType(index)}
                            disabled={types.length === 1}
                          >
                            <X className="h-4 w-4" />
                          </Button>
                        </div>
                      ))}
                      {errors.type && (
                        <p className="text-sm text-destructive">{errors.type.message}</p>
                      )}
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="description" className="text-sm font-medium">{t("admin.description")}</Label>
                      <RichTextEditor
                        value={watch("description") || ""}
                        onChange={(value) => setValue("description", value)}
                        placeholder={t("admin.enterDescription")}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="announcement_zh" className="text-sm font-medium">{t("admin.announcement")}</Label>
                      <Textarea 
                        id="announcement_zh" 
                        {...register("announcement_zh")} 
                        rows={2}
                        placeholder={t("admin.enterAnnouncement")}
                      />
                      <p className="text-xs text-muted-foreground">{t("admin.announcementHint")}</p>
                    </div>
                  </TabsContent>
                  
                  <TabsContent value="en" className="space-y-4 mt-0">
                    <div className="space-y-2">
                      <Label htmlFor="name_en" className="text-sm font-medium">Restaurant Name</Label>
                      <Input 
                        id="name_en" 
                        {...register("name_en")} 
                        placeholder="Enter restaurant name..."
                        className="text-base"
                      />
                      {errors.name_en && (
                        <p className="text-sm text-destructive">{errors.name_en.message}</p>
                      )}
                    </div>
                    <div className="space-y-3">
                      <Label className="text-sm font-medium">Restaurant Types (1-3 tags)</Label>
                      {types.map((type, index) => (
                        <div key={index} className="flex gap-2 items-center">
                          <TypeCombobox
                            placeholder="e.g. Cantonese"
                            value={type.en}
                            onChange={(value) => updateType(index, 'en', value)}
                            suggestions={existingTypes?.uniqueEn || []}
                          />
                        </div>
                      ))}
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="description_en" className="text-sm font-medium">Description</Label>
                      <RichTextEditor
                        value={watch("description_en") || ""}
                        onChange={(value) => setValue("description_en", value)}
                        placeholder="Enter restaurant description..."
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="announcement_en" className="text-sm font-medium">Announcement</Label>
                      <Textarea 
                        id="announcement_en" 
                        {...register("announcement_en")} 
                        rows={2}
                        placeholder="Enter announcement (optional)..."
                      />
                      <p className="text-xs text-muted-foreground">Display important notices at top of restaurant page</p>
                    </div>
                  </TabsContent>
                  
                  <TabsContent value="pt" className="space-y-4 mt-0">
                    <div className="space-y-2">
                      <Label htmlFor="name_pt" className="text-sm font-medium">Nome do Restaurante</Label>
                      <Input 
                        id="name_pt" 
                        {...register("name_pt")} 
                        placeholder="Digite o nome do restaurante..."
                        className="text-base"
                      />
                      {errors.name_pt && (
                        <p className="text-sm text-destructive">{errors.name_pt.message}</p>
                      )}
                    </div>
                    <div className="space-y-3">
                      <Label className="text-sm font-medium">Tipos de Restaurante (1-3 tags)</Label>
                      {types.map((type, index) => (
                        <div key={index} className="flex gap-2 items-center">
                          <TypeCombobox
                            placeholder="ex: Cantonesa"
                            value={type.pt}
                            onChange={(value) => updateType(index, 'pt', value)}
                            suggestions={existingTypes?.uniquePt || []}
                          />
                        </div>
                      ))}
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="description_pt" className="text-sm font-medium">Descrição</Label>
                      <RichTextEditor
                        value={watch("description_pt") || ""}
                        onChange={(value) => setValue("description_pt", value)}
                        placeholder="Digite a descrição do restaurante..."
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="announcement_pt" className="text-sm font-medium">Anúncio</Label>
                      <Textarea 
                        id="announcement_pt" 
                        {...register("announcement_pt")} 
                        rows={2}
                        placeholder="Digite o anúncio (opcional)..."
                      />
                      <p className="text-xs text-muted-foreground">Exibir avisos importantes no topo da página do restaurante</p>
                    </div>
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>

            {/* Location and Contact */}
            <Card className="border-primary/20">
              <CardContent className="p-6 space-y-4">
                <Label className="text-base font-semibold flex items-center gap-2">
                  <MapPin className="w-5 h-5 text-primary" />
                  {t("admin.locationAndContact")}
                </Label>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="address" className="text-sm font-medium flex items-center gap-2">
                      <MapPin className="w-4 h-4" />
                      {t("admin.address")}
                    </Label>
                    <Input id="address" {...register("address")} placeholder={t("admin.enterAddress")} />
                    {errors.address && (
                      <p className="text-sm text-destructive">{errors.address.message}</p>
                    )}
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="phone" className="text-sm font-medium flex items-center gap-2">
                      <Phone className="w-4 h-4" />
                      {t("admin.phone")}
                    </Label>
                    <Input id="phone" {...register("phone")} placeholder="+351..." />
                  </div>
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="website_url" className="text-sm font-medium flex items-center gap-2">
                      <WebIcon className="w-4 h-4" />
                      {t("admin.websiteBaseUrl")}
                    </Label>
                    <Input 
                      id="website_url" 
                      {...register("website_url")} 
                      placeholder="https://sushi.xiaoxiong.pt:6000" 
                      readOnly={!isAdmin}
                      disabled={!isAdmin}
                      className={!isAdmin ? "bg-muted cursor-not-allowed" : ""}
                    />
                    {!isAdmin && (
                      <p className="text-xs text-muted-foreground">{t("admin.websiteReadOnly")}</p>
                    )}
                  </div>
                  {isAdmin && (
                    <div className="space-y-2">
                      <Label htmlFor="key_id" className="text-sm font-medium flex items-center gap-2">
                        <Key className="w-4 h-4" />
                        Key ID (API)
                      </Label>
                      <Input 
                        id="key_id" 
                        {...register("key_id")} 
                        placeholder="例如: saldanha01" 
                      />
                    </div>
                  )}
                </div>
                {/* Button visibility toggles */}
                <div className="space-y-3 pt-2">
                  <Label className="text-sm font-medium text-muted-foreground">
                    {t("admin.displaySettings")}
                  </Label>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                    <div className="flex items-center space-x-2 p-3 rounded-lg border bg-muted/30">
                      <Checkbox
                        id="show_takeaway_button"
                        checked={showTakeawayButton}
                        onCheckedChange={(checked) => setValue("show_takeaway_button", checked as boolean)}
                      />
                      <Label htmlFor="show_takeaway_button" className="cursor-pointer text-sm flex items-center gap-2">
                        <ShoppingBag className="w-4 h-4" />
                        {t("admin.showTakeawayButton")}
                      </Label>
                    </div>
                    {/* Only show booking button toggle for non-ghost templates */}
                    {template !== 'ghost' && (
                      <div className="flex items-center space-x-2 p-3 rounded-lg border bg-muted/30">
                        <Checkbox
                          id="show_booking_button"
                          checked={showBookingButton}
                          onCheckedChange={(checked) => setValue("show_booking_button", checked as boolean)}
                        />
                        <Label htmlFor="show_booking_button" className="cursor-pointer text-sm flex items-center gap-2">
                          <UtensilsCrossed className="w-4 h-4" />
                          {t("admin.showBookingButton")}
                        </Label>
                      </div>
                    )}
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Owner Profile Section - Only for Ghost Template */}
            {template === 'ghost' && (
              <Card className="border-primary/20 bg-gradient-to-br from-background to-muted/20">
                <CardContent className="p-6">
                  <Label className="text-base font-semibold flex items-center gap-2 mb-4">
                    <User className="w-5 h-5 text-primary" />
                    {t("admin.ghostTemplateNote")}
                  </Label>
                  
                  {/* Owner Avatar Upload */}
                  <div className="space-y-4 mb-6">
                    <div className="flex items-center gap-2">
                      <span className="text-sm font-medium">{t("admin.ownerAvatar")}</span>
                    </div>
                    <div className="flex items-center gap-4">
                      {ownerAvatarPreview && (
                        <div className="relative group">
                          <img
                            src={ownerAvatarPreview}
                            alt="Owner Avatar Preview"
                            className="w-20 h-20 object-cover rounded-full shadow-lg ring-2 ring-primary/20 transition-transform duration-200 group-hover:scale-105"
                          />
                          <Button
                            type="button"
                            variant="destructive"
                            size="icon"
                            className="absolute -top-2 -right-2 h-6 w-6 shadow-lg opacity-0 group-hover:opacity-100 transition-opacity rounded-full"
                            onClick={() => {
                              setOwnerAvatarFile(null);
                              setOwnerAvatarPreview(null);
                            }}
                          >
                            <X className="h-3 w-3" />
                          </Button>
                        </div>
                      )}
                      <Label htmlFor="owner-avatar-upload" className="cursor-pointer">
                        <div className="flex flex-col items-center gap-2 px-6 py-4 border-2 border-dashed border-primary/30 rounded-lg hover:border-primary hover:bg-primary/5 transition-all duration-200">
                          <User className="h-6 w-6 text-primary" />
                          <div className="text-center">
                            <p className="font-medium text-xs">{t("admin.uploadImage")}</p>
                          </div>
                        </div>
                        <Input
                          id="owner-avatar-upload"
                          type="file"
                          accept="image/*"
                          className="hidden"
                          onChange={handleOwnerAvatarChange}
                        />
                      </Label>
                    </div>
                  </div>
                  
                  {/* Owner Bio - Multi-language */}
                  <div className="space-y-4">
                    <Label className="text-sm font-medium">{t("admin.ownerBio")}</Label>
                    <Tabs defaultValue="zh" className="w-full">
                      <TabsList className="grid w-full grid-cols-3 mb-4">
                        <TabsTrigger value="zh" className="gap-2 text-xs">
                          🇨🇳 中文
                        </TabsTrigger>
                        <TabsTrigger value="en" className="gap-2 text-xs">
                          🇬🇧 EN
                        </TabsTrigger>
                        <TabsTrigger value="pt" className="gap-2 text-xs">
                          🇵🇹 PT
                        </TabsTrigger>
                      </TabsList>
                      
                      <TabsContent value="zh">
                        <Textarea 
                          value={ownerBio}
                          onChange={(e) => setOwnerBio(e.target.value)}
                          rows={4}
                          placeholder={t("admin.ownerBioPlaceholder")}
                        />
                      </TabsContent>
                      
                      <TabsContent value="en">
                        <Textarea 
                          value={ownerBioEn}
                          onChange={(e) => setOwnerBioEn(e.target.value)}
                          rows={4}
                          placeholder="Share your story, philosophy and culinary passion..."
                        />
                      </TabsContent>
                      
                      <TabsContent value="pt">
                        <Textarea 
                          value={ownerBioPt}
                          onChange={(e) => setOwnerBioPt(e.target.value)}
                          rows={4}
                          placeholder="Compartilhe sua história, filosofia e paixão culinária..."
                        />
                      </TabsContent>
                    </Tabs>
                  </div>
                </CardContent>
              </Card>
            )}

            {/* Map Location - Only for non-ghost templates */}
            {template !== 'ghost' && (
              <Card className="border-primary/20">
                <CardContent className="p-6 space-y-4">
                  <Label className="text-base font-semibold flex items-center gap-2">
                    <MapPin className="w-5 h-5 text-primary" />
                    {t("admin.location")}
                  </Label>
                  <div className="grid grid-cols-2 gap-4 mb-2">
                    <div className="space-y-2">
                      <Label htmlFor="lat" className="text-sm font-medium">{t("admin.latitude")}</Label>
                      <Input
                        id="lat"
                        type="number"
                        step="any"
                        {...register("lat", { valueAsNumber: true })}
                      />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="lng" className="text-sm font-medium">{t("admin.longitude")}</Label>
                      <Input
                        id="lng"
                        type="number"
                        step="any"
                        {...register("lng", { valueAsNumber: true })}
                      />
                    </div>
                  </div>
                  <div ref={mapContainer} className="h-96 w-full rounded-lg border-2 border-primary/20 shadow-sm" style={{ minHeight: '384px' }} />
                  <p className="text-sm text-muted-foreground flex items-center gap-2">
                    <MapPin className="w-4 h-4" />
                    {t("admin.clickOrDragToSetLocation")}
                  </p>
                </CardContent>
              </Card>
            )}

            {/* Opening Hours - Only for non-ghost templates */}
            {template !== 'ghost' && (
              <Card className="border-primary/20 overflow-hidden">
                <CardContent className="p-3 md:p-6 space-y-4">
                  <Label className="text-base font-semibold flex items-center gap-2">
                    <Clock className="w-5 h-5 text-primary" />
                    {t("admin.openingHours")}
                  </Label>
                  <div className="space-y-2">
                    {DAYS.map((day) => (
                      <div key={day} className="space-y-2 p-2 md:p-3 rounded-lg hover:bg-muted/50 transition-colors">
                        {/* Mobile Layout */}
                        <div className="md:hidden space-y-2">
                          <div className="flex items-center justify-between">
                            <Label className="capitalize font-semibold text-sm">{t(`admin.day.${day}`)}</Label>
                            <div className="flex items-center gap-2">
                              <div className="flex items-center gap-1">
                                <Checkbox
                                  id={`split-mobile-${day}`}
                                  checked={openingHours[day].splitShift}
                                  onCheckedChange={(checked) =>
                                    setOpeningHours({
                                      ...openingHours,
                                      [day]: { ...openingHours[day], splitShift: checked as boolean },
                                    })
                                  }
                                  disabled={openingHours[day].closed}
                                  className="h-4 w-4"
                                />
                                <Label htmlFor={`split-mobile-${day}`} className="cursor-pointer text-xs">
                                  分段
                                </Label>
                              </div>
                              <div className="flex items-center gap-1">
                                <Checkbox
                                  id={`closed-mobile-${day}`}
                                  checked={openingHours[day].closed}
                                  onCheckedChange={(checked) =>
                                    setOpeningHours({
                                      ...openingHours,
                                      [day]: { ...openingHours[day], closed: checked as boolean },
                                    })
                                  }
                                  className="h-4 w-4"
                                />
                                <Label htmlFor={`closed-mobile-${day}`} className="cursor-pointer text-xs">
                                  {t("admin.closed")}
                                </Label>
                              </div>
                            </div>
                          </div>
                          
                          {!openingHours[day].closed && (
                            <div className="space-y-1.5">
                              {openingHours[day].splitShift ? (
                                <div className="space-y-1.5">
                                  <div className="grid grid-cols-[32px_1fr_12px_1fr] gap-1 items-center">
                                    <span className="text-xs text-muted-foreground">午餐</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].open}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], open: e.target.value },
                                        })
                                      }
                                      className="h-8 text-xs px-2 min-w-0"
                                    />
                                    <span className="text-muted-foreground text-xs text-center">-</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].close}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], close: e.target.value },
                                        })
                                      }
                                      className="h-8 text-xs px-2 min-w-0"
                                    />
                                  </div>
                                  <div className="grid grid-cols-[32px_1fr_12px_1fr] gap-1 items-center">
                                    <span className="text-xs text-muted-foreground">晚餐</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].open2 || "19:00"}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], open2: e.target.value },
                                        })
                                      }
                                      className="h-8 text-xs px-2 min-w-0"
                                    />
                                    <span className="text-muted-foreground text-xs text-center">-</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].close2 || "23:00"}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], close2: e.target.value },
                                        })
                                      }
                                      className="h-8 text-xs px-2 min-w-0"
                                    />
                                  </div>
                                </div>
                              ) : (
                                <div className="grid grid-cols-[1fr_12px_1fr] gap-1 items-center">
                                  <Input
                                    type="time"
                                    value={openingHours[day].open}
                                    onChange={(e) =>
                                      setOpeningHours({
                                        ...openingHours,
                                        [day]: { ...openingHours[day], open: e.target.value },
                                      })
                                    }
                                    className="h-8 text-xs px-2 min-w-0"
                                  />
                                  <span className="text-muted-foreground text-xs text-center">-</span>
                                  <Input
                                    type="time"
                                    value={openingHours[day].close}
                                    onChange={(e) =>
                                      setOpeningHours({
                                        ...openingHours,
                                        [day]: { ...openingHours[day], close: e.target.value },
                                      })
                                    }
                                    className="h-8 text-xs px-2 min-w-0"
                                  />
                                </div>
                              )}
                            </div>
                          )}
                        </div>

                        {/* Desktop Layout */}
                        <div className="hidden md:block">
                          <div className="grid grid-cols-[120px_1fr_auto] gap-4 items-center">
                            <Label className="capitalize font-semibold">{t(`admin.day.${day}`)}</Label>
                            <div className="flex items-center gap-2">
                              <Checkbox
                                id={`split-${day}`}
                                checked={openingHours[day].splitShift}
                                onCheckedChange={(checked) =>
                                  setOpeningHours({
                                    ...openingHours,
                                    [day]: { ...openingHours[day], splitShift: checked as boolean },
                                  })
                                }
                                disabled={openingHours[day].closed}
                              />
                              <Label htmlFor={`split-${day}`} className="cursor-pointer text-sm">
                                分段营业
                              </Label>
                            </div>
                            <div className="flex items-center space-x-2">
                              <Checkbox
                                id={`closed-${day}`}
                                checked={openingHours[day].closed}
                                onCheckedChange={(checked) =>
                                  setOpeningHours({
                                    ...openingHours,
                                    [day]: { ...openingHours[day], closed: checked as boolean },
                                  })
                                }
                              />
                              <Label htmlFor={`closed-${day}`} className="cursor-pointer">
                                {t("admin.closed")}
                              </Label>
                            </div>
                          </div>
                          
                          {!openingHours[day].closed && (
                            <div className="ml-[120px] space-y-2 mt-2">
                              {openingHours[day].splitShift ? (
                                <div className="space-y-2">
                                  <div className="flex items-center gap-2">
                                    <span className="text-sm text-muted-foreground w-12">午餐</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].open}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], open: e.target.value },
                                        })
                                      }
                                      className="w-32"
                                    />
                                    <span className="text-muted-foreground">-</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].close}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], close: e.target.value },
                                        })
                                      }
                                      className="w-32"
                                    />
                                  </div>
                                  <div className="flex items-center gap-2">
                                    <span className="text-sm text-muted-foreground w-12">晚餐</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].open2 || "19:00"}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], open2: e.target.value },
                                        })
                                      }
                                      className="w-32"
                                    />
                                    <span className="text-muted-foreground">-</span>
                                    <Input
                                      type="time"
                                      value={openingHours[day].close2 || "23:00"}
                                      onChange={(e) =>
                                        setOpeningHours({
                                          ...openingHours,
                                          [day]: { ...openingHours[day], close2: e.target.value },
                                        })
                                      }
                                      className="w-32"
                                    />
                                  </div>
                                </div>
                              ) : (
                                <div className="flex items-center gap-2">
                                  <Input
                                    type="time"
                                    value={openingHours[day].open}
                                    onChange={(e) =>
                                      setOpeningHours({
                                        ...openingHours,
                                        [day]: { ...openingHours[day], open: e.target.value },
                                      })
                                    }
                                    className="w-32"
                                  />
                                  <span className="text-muted-foreground">-</span>
                                  <Input
                                    type="time"
                                    value={openingHours[day].close}
                                    onChange={(e) =>
                                      setOpeningHours({
                                        ...openingHours,
                                        [day]: { ...openingHours[day], close: e.target.value },
                                      })
                                    }
                                    className="w-32"
                                  />
                                </div>
                              )}
                            </div>
                          )}
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            )}
          </form>
        </ScrollArea>

        <div className="flex justify-end gap-2 px-6 py-4 border-t bg-muted/30">
          <Button type="button" variant="outline" onClick={() => setOpen(false)}>
            {t("common.cancel")}
          </Button>
          <Button type="submit" disabled={isSubmitting} onClick={handleSubmit(onSubmit)} className="gap-2">
            <Store className="w-4 h-4" />
            {isSubmitting ? t("common.saving") : t("common.save")}
          </Button>
        </div>
      </DialogContent>
    </Dialog>
  );
}
